<template>
  <div class="InventionPatentTotalBackground">
    <!--     <input
            type="text"
            class="InventionPatentUserNumberInput"
            v-model="InventionPatentUserNumber"
            placeholder="请输入学生学号"
          />
          <button  class="InventionPatentSeekButton" @click="InventionPatentSeekButtonClick">查询</button>
          <button  class="InventionPatentEditButton" @click="InventionPatentEditButtonClick">编辑</button>
       -->
    <div class="InventionPatentTableBackground">
      <table>
        <thead>
          <tr>
            <th style="width: 2%">学号</th>
            <th style="width: 2%">姓名</th>
            <th style="width: 6%">专利名称</th>
            <th style="width: 2%">专利号</th>
            <th style="width: 2%">专利申请日期</th>
            <th style="width: 2%">专利权人</th>
            <th style="width: 2%">专利授权公告日期</th>
            <th style="width: 1%">颁发部门</th>
            <th style="width: 1%">详情</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="records in StudentInventionPatentTable"
            :key="records.studentNumber"
          >
            <!-- 如果在tr标签里写点击事件就是一行  @click="handleRowClick(records.id)" -->
            <td>{{ records.developmentPatents[0].studentNumber }}</td>
            <td>{{ records.developmentPatents[0].studentName }}</td>
            <td>{{ records.developmentPatents[0].patentName }}</td>
            <td>{{ records.developmentPatents[0].patentNumber }}</td>
            <td>{{ records.developmentPatents[0].patentApplicationTime }}</td>
            <td>{{ records.developmentPatents[0].patentee }}</td>
            <td>
              {{ records.developmentPatents[0].authorizationAnnouncementDate }}
            </td>
            <td>{{ records.developmentPatents[0].issuingDepartment }}</td>
            <td @click="ViewDetails(records)" class="Details">查看</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="InventionPatentPagination">
      <el-pagination
        size="small"
        :page-size="pageSize"
        :pager-count="10"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handlePageChange"
      />
    </div>
    <div v-if="ifDetails" class="InventionPatentDetailsBackground">
      <div class="InventionPatentDetailsFormBackground">
        <span class="InventionPatentDetailsFormTitle">软著详细信息</span>
        <form class="InventionPatentDetailsForm">
          <div class="label-row">
            <label>姓名 : {{ studentDetails[0].studentName }}</label>
            <label>学号 : {{ studentDetails[0].studentNumber }}</label>
          </div>
          <div v-for="(data, index) in studentDetails" :key="index">
            <div class="label-row">
              <label>专利名称 : {{ data.patentName }}</label>
            </div>
            <div class="label-row">
              <label>专利号 : {{ data.patentNumber }}</label>
            </div>
            <div class="label-row">
              <label>专利申请日期 : {{ data.patentApplicationTime }}</label>
            </div>
            <div class="label-row">
              <label>专利权人 : {{ data.patentee }}</label>
            </div>
            <div class="label-row">
              <label
                >专利授权公告日期 :
                {{ data.authorizationAnnouncementDate }}</label
              >
            </div>
            <div class="label-row">
              <label>颁发部门 : {{ data.issuingDepartment }}</label>
            </div>
            <br />
          </div>
        </form>
        <button
          class="InventionPatentDetailsFormCloseButton"
          @click="InventionPatentDetailsFormCloseButtonClick"
        >
          关闭
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "@/axios";

export default {
  data() {
    return {
      StudentInventionPatentTable: [],
      ifDetails: false,
      studentDetails: {},
      currentPage: 1,
      pageSize: 12,
      total: 130,
    };
  },
  mounted() {
    this.FetchData();
  },
  methods: {
    handlePageChange(page) {
      this.currentPage = page;
      this.FetchData();
    },

    async FetchData() {
      try {
        const informationResponse = await axios.get(
          "/admin/getAcademicPerformance/getDevelopmentPatent",
          {
            params: {
              page: this.currentPage,
              pageSize: this.pageSize,
            },
          }
        );
        if (informationResponse.code == 1) {
          this.total = informationResponse.data.total;
          this.StudentInventionPatentTable = informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },
    ViewDetails(records) {
      this.RenewLabel(records);
      this.ifDetails = true;
    },
    RenewLabel(response) {
      this.studentDetails = response.developmentPatents;
    },
    InventionPatentDetailsFormCloseButtonClick() {
      this.ifDetails = false;
    },
  },
};
</script>

<style scoped>
.Details {
  cursor: pointer;
  text-decoration: underline;
}
.Details:hover {
  color: #8491cd; /* 可选：当鼠标悬停时改变颜色 */
}
.InventionPatentPagination {
  position: absolute;
  top: 95%;
}
.InventionPatentUserNumberInput {
  /* position: absolute; */
  border-radius: 5px;
  outline: none; /* 外边线颜色不要 */
  border: none; /* 边框颜色不要 */
}

.InventionPatentTotalBackground {
  position: relative;
  width: 100%;
  height: 100%;
}
.InventionPatentTableBackground {
  position: absolute;
  width: 100%;
  height: 90%;
  overflow: auto;
}

@import "/src/components/ManageSystem/TableStyle.css";
@import "/src/components/ManageSystem/DetailsForm/InventionPatentForm.css";
@import "/src/components/ManageSystem/MouseScrollWheelStyle/DetailFormMouse.css";
</style>
